html,
body {
  height: 100%;
  margin: 0;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content {
  height: 100vh;
  /*margin-left: 18rem;
  border-radius: 10px 10px 10px 10px;*/
}

.navbar{
  background-color: bisque !important;
}

.register-page{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

/*p.nav-link {
  margin-top: 10px;
}*/

.ant-col.ant-form-item-label.css-dev-only-do-not-override-kghr11 {
  font-weight: bold;
}

a {
  font-weight: bold;
}

.text-center {
  text-align: center !important;
  font-weight: bold;
}

button.btn.btn-primary {
  font-weight: bold;
}

.register-page{
  background-image: url('./assets/login.jpeg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 50% 70%;
}

.cardd{
  border-radius: 15px 50px;
}

p.nav-link {
  font-weight: bold;
  margin-top: 0.5rem;
}

.log_out{
  margin-top: 0.70rem;
}

.p-4 {
  padding: 1.5rem !important;
  margin-top: 23.8rem;
  position: static;
}


.category{
  margin-top: -0.50rem;
}

:where(.css-dev-only-do-not-override-kghr11).ant-table-wrapper {
  clear: both;
  max-width: 100%;
  margin-left: 10rem;
  margin-right: 10rem;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
  border-radius: 10px 10px 10px 10px;
}

.analytics{
  margin-left: 8.3rem;
}




/*Transaction Page*/
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
  margin-left: 10rem;
  margin-right: 10rem;
  border-radius: 10px 10px 10px 10px;
}

/*Antd Icons*/
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
  color: black;
}

.inactive-icon{
  color: gray;
}

.switch-icon{
  border: 1px solid rgba(0,0,0,0.677);
  border-radius: 5px;
  padding: 10px 15px;
}